<template>
    <router-view></router-view>
</template>

<script setup lang="ts">
import {
    chatGroupList,
    chatGroupListLoading,
    changeChatGroup,
    addCommonChatGroup,
} from '@/hooks/useChatGroupList';
import { useAppStore } from '@/store/app';
import { watch } from 'vue';
import { useRouter } from 'vue-router';

const router = useRouter();

const appStore = useAppStore();

watch(
    chatGroupListLoading,
    async () => {
        if (chatGroupListLoading.value) {
            return;
        }
        // if (chatGroupList.value.length === 0) {

        if (appStore.autoSend) {
            router.replace({
                path: '/chat/new',
                query: {
                    agent: appStore.autoSend.agent,
                },
            });
        } else {
            router.replace({
                path: '/chat/new',
                query: {
                    agent: 'super',
                },
            });
        }
        // await addCommonChatGroup();
        // } else {
        //     changeChatGroup(
        //         chatGroupList.value[chatGroupList.value.length - 1].groupCode,
        //     );
        // }
    },
    {
        immediate: true,
    },
);
</script>
